import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { FormatterService } from '../../services/local/formatter/formatter.service';

@Component({
  selector: 'app-generacion-de-ordenes',
  templateUrl: './generacion-de-ordenes.component.html',
  styleUrls: ['./generacion-de-ordenes.component.scss']
})
export class GeneracionDeOrdenesComponent implements OnInit {

  public groupForm: FormGroup;
  showMotiv:boolean = true;
  
  constructor(
    private formBuilder: FormBuilder,
    private formatterService:FormatterService) {

      this.formatterService.actualizarSeccionActiva('seccionGeneracionOrdenes');
    }

  ngOnInit() {
    this.groupForm = this.formBuilder.group({
      concluido: true,
    });
  }

  selectChangeHandler(event: any) {
    // si es tiene pase mdico
    this.showMotiv = event.target.value;
  }
  
  onSubmit(){
    var element: HTMLElement = document.getElementById('termino-atencion') as HTMLElement;
    element.click();
  }
}
