import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { FormatterService } from '../../services/local/formatter/formatter.service';
import { StorageService } from '../../services/local/storage/storage.service';

@Component({
  selector: 'app-punto-de-choque-y-danos',
  templateUrl: './punto-de-choque-y-danos.component.html',
  styleUrls: ['./punto-de-choque-y-danos.component.scss']
})
export class PuntoDeChoqueYDanosComponent implements OnInit {

  public groupForm: FormGroup;
  showAuto: boolean = false;
  showMoto: boolean = false;
  showVan: boolean = false;
  showName: string = 'Tipo';
  showMedicForm: boolean = false;

  constructor(
    private formatterService:FormatterService,
    private storageService:StorageService,
    private formBuilder: FormBuilder) {}

  ngOnInit() {
    this.formatterService.actualizarSubSeccionActiva('seccionPuntosDeChoque');

    this.groupForm = this.formBuilder.group({
      tipo_auto: '',
    });
  }

  selectChangeHandler(event: any) {
    // si es 
    if (event.target.value == 1) {
      this.showAuto = true;
      this.showMoto = false;
      this.showVan = false;
      this.showName = 'Automvil';
    } else if(event.target.value == 2) {
      this.showAuto = false;
      this.showMoto = false;
      this.showVan = true;
      this.showName = 'Vagoneta';
    } else if(event.target.value == 3) {
      this.showAuto = false;
      this.showMoto = true;
      this.showVan = false;
      this.showName = 'Motocicleta';
    }
    else{
      this.showAuto = false;
      this.showMoto = false;
      this.showVan = false;
      this.showName = 'Elige una opcin';
    }
  }

  onSubmit(){
    var element: HTMLElement = document.getElementById('circunstancia') as HTMLElement;
    element.click();
  }
}
