import { Injectable } from '@angular/core';
import { Observable, Subject }    from 'rxjs';
import * as _ from 'lodash';    

@Injectable({
  providedIn: 'root'
})
export class StorageService {
  subFlujoActivo:string;
  registro:any;
  SubRegistro = new Subject<any>();

  constructor() { }

  async setLSRegistro(){}
  async getLSRegistro(){}

  setRegistro(data:any){
    this.registro = data;
    this.SubRegistro.next(data);
  }

  getRegistro(){
    return _.cloneDeep(this.registro);
  }
  
  updateRegistro(): Observable<any>{
    return this.SubRegistro.asObservable();
  }


  setFlagSubFlujo(id){
    this.subFlujoActivo = id;
  }
  updateFlagSubFlujo(){
    return this.subFlujoActivo;
  }
}
