import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormatterService } from '../../services/local/formatter/formatter.service';
import { StorageService } from '../../services/local/storage/storage.service';

import 'fabric';
declare const fabric: any;

@Component({
  selector: 'app-croquis-de-accidente',
  templateUrl: './croquis-de-accidente.component.html',
  styleUrls: ['./croquis-de-accidente.component.scss']
})
export class CroquisDeAccidenteComponent implements OnInit {
  
  // canvas
  @ViewChild('canvas')
  canvasElement: ElementRef;
  canvas: any;

  // el activo del men
  isActive:number;

  // encaps. texto
  textString: string;

  properties: any = {
    canvasFill: null,
    canvasImage: null,
    id: null
  };

  constructor(
    private formatterService:FormatterService,
    private storageService:StorageService,
    private modalService: NgbModal) {}

  openModal(content) {
      this.modalService.open(content, { centered: true });
  }
  
  ngOnInit() {
    this.formatterService.actualizarSubSeccionActiva('seccionCroquis');

    this.canvas = new fabric.Canvas('canvas', {
      hoverCursor: 'pointer',
      selection: false,
    });

    this.canvas.on('mouse:move', (e) => {
      if (this.isActive == 1) {
        this.canvas.isDrawingMode = 1;
        this.canvas.freeDrawingBrush.color = '#000000';
        this.canvas.freeDrawingBrush.width = 3;
        this.canvas.renderAll();
      } else {
        this.canvas.isDrawingMode = 0;
      }
    });
  }

  randomObjectId() {
    return Math.floor(Math.random() * 999999) + 1;
  }

  extend(object, id) {
    object.toObject = (function (toObject) {
      return function () {
        return fabric.util.object.extend(toObject.call(this), {
          id: id
        });
      };
    })(object.toObject);
  }

  addString() {
    if (!this.textString) return;

    let text = new fabric.IText(this.textString, {
      left: 30,
      top: 30,
      fontFamily: 'Arial',
      fill: '#000000',
      scaleX: 0.5,
      scaleY: 0.5,
      hasRotatingPoint: true,
    });
    this.setObject(text);
    this.textString = null;
  }

  addImage(event: any) {
    let img = event.target;

    fabric.Image.fromURL(img.src, (image) => {
      image.set({
        left: 30,
        top: 30,
        padding: 5,
        scaleX: 0.3,
        scaleY: 0.3,
        hasRotatingPoint: true
      });
      this.setObject(image);
    });
  }

  setObject(object) {
    this.extend(object, this.randomObjectId());
    this.canvas.add(object);
    this.canvas.setActiveObject(object);
  }

  removeSelected() {
    let active = this.canvas.getActiveObject();

    if (active) {
      this.canvas.remove(active);
    }
  }

  openCanvas() {
    this.canvasElement.nativeElement.parentElement.classList.add('canvas-show');
  }

  toggleCanvas() {
    this.canvasElement.nativeElement.parentElement.classList.toggle('canvas-show');
  }



  // Faltantes --->
  onSubmit() {
    let element: HTMLElement = document.getElementById('generacion-ordenes') as HTMLElement;
    element.click();
  }

  saveCanvas() {}
}
