import { Pipe, PipeTransform } from '@angular/core';
import * as config from '../../services/configurations';


@Pipe({
  name: 'canAccess'
})
export class CanAccessPipe implements PipeTransform {

  transform(items: any, args?: any): any {

    let itemsFiltred: any[] = [];
    let permisos: any = config.CONTENIDO_ROLES_PERMISOS;
    let usuario:  any = config.USUARIO;

    if(items && permisos && usuario){

      items.forEach(item => {
        if(!item.childs){

          permisos[item.modulo][item.accion].permisosRol.forEach(rol => {
            if(rol == usuario.rol)itemsFiltred.push(item);
          });

        }else if(item.childs.length != 0){

          let childsFiltred: any[] = [];
          
          item.childs.forEach(child => {
            permisos[child.modulo][child.accion].permisosRol.forEach(rol => {
              if(rol == usuario.rol)childsFiltred.push(child);
            });
          });

          if(childsFiltred.length != 0){
            item.childs = childsFiltred;
            itemsFiltred.push(item)
          };
          
        }
      });

      return itemsFiltred;

    }else{

      return items;
      
    }
  }

}
