import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { NgbTimepickerConfig } from '@ng-bootstrap/ng-bootstrap';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormatterService } from '../../services/local/formatter/formatter.service';
import { StorageService } from '../../services/local/storage/storage.service';

import * as Labels from '../../interfaces/labels';
import * as Inputs from '../../interfaces/inputs';

@Component({
  selector: 'app-datos-de-siniestro',
  templateUrl: './datos-de-siniestro.component.html',
  styleUrls: ['./datos-de-siniestro.component.scss']
})
export class DatosDeSiniestroComponent implements OnInit {
  params:string;
  header:any;
  badge:any;
  company:string;
  role:string;
  folio:string;
  inputs:any;
  labels:any;

  groupForm: FormGroup;

  // hidden
  showCity: boolean = false;
  showHighway: boolean = false;

  constructor(
    public route:ActivatedRoute,
    public formBuilder: FormBuilder,
    private formatterService:FormatterService,
    private storageService:StorageService,
    config: NgbTimepickerConfig, private modalService: NgbModal) {

      
      config.spinners = false;
      config.seconds = true;

    // inputs
    this.inputs = {
      cita_programada: Inputs.datosDeSiniestro.cita_programada,
      fecha_ocurrido: Inputs.datosDeSiniestro.fecha_ocurrido,
      fecha_atencion: Inputs.datosDeSiniestro.fecha_atencion,
      hora_ocurrido: Inputs.datosDeSiniestro.hora_ocurrido,
      hora_atencion: Inputs.datosDeSiniestro.hora_atencion,
      lugar_siniestro: Inputs.datosDeSiniestro.lugar_siniestro,
      tipo_vialidad: Inputs.datosDeSiniestro.tipo_vialidad,
      nombre_calle: Inputs.datosDeSiniestro.nombre_calle,
      num_exterior: Inputs.datosDeSiniestro.num_exterior,
      num_interior: Inputs.datosDeSiniestro.num_interior,
      cod_postal: Inputs.datosDeSiniestro.cod_postal,
      colonia: Inputs.datosDeSiniestro.colonia,
      municipio: Inputs.datosDeSiniestro.municipio,
      estado: Inputs.datosDeSiniestro.estado,
      calle1_tipo: Inputs.datosDeSiniestro.calle1_tipo,
      calle1_nombre: Inputs.datosDeSiniestro.calle1_nombre,
      calle2_tipo: Inputs.datosDeSiniestro.calle2_tipo,
      calle2_nombre: Inputs.datosDeSiniestro.calle2_nombre,
      tipo_carretera: Inputs.datosDeSiniestro.tipo_carretera,
      nombre_carretera: Inputs.datosDeSiniestro.nombre_carretera,
      num_kilometro: Inputs.datosDeSiniestro.num_kilometro,
      observaciones: Inputs.datosDeSiniestro.observaciones
    } 

    this.groupForm = this.formBuilder.group({
      [this.inputs.cita_programada]: [false, Validators.required],
      [this.inputs.fecha_ocurrido]: ['', Validators.required],
      [this.inputs.fecha_atencion]: ['', Validators.required],
      [this.inputs.hora_ocurrido]: ['', Validators.required],
      [this.inputs.hora_atencion]: ['', Validators.required],
      [this.inputs.lugar_siniestro]: ['', Validators.required],
      [this.inputs.tipo_vialidad]: ['', Validators.required],
      [this.inputs.nombre_calle]: ['', Validators.required],
      [this.inputs.num_exterior]: '',
      [this.inputs.num_interior]: '',
      [this.inputs.cod_postal]: ['', Validators.required],
      [this.inputs.colonia]: ['', Validators.required],
      [this.inputs.municipio]: ['', Validators.required],
      [this.inputs.estado]: ['', Validators.required],
      [this.inputs.calle1_tipo]: '',
      [this.inputs.calle1_nombre]: '',
      [this.inputs.calle2_tipo]: '',
      [this.inputs.calle2_nombre]: '',
      [this.inputs.tipo_carretera]: ['', Validators.required],
      [this.inputs.nombre_carretera]: ['', Validators.required],
      [this.inputs.num_kilometro]: ['', Validators.required],
      [this.inputs.observaciones]: '',
    });
  }

  selectChangeHandler(event: any) {
    this.showCity    = false;
    this.showHighway = false;

    if (event.target.value == 1) {
      this.showCity    = true;
    } else if(event.target.value == 2) {
      this.showHighway = true;
    } 
  }

  ngOnInit() {
    this.formatterService.actualizarSubSeccionActiva('seccionSiniestro');

    this.header = Labels.datosDeSiniestro.header;
    this.badge  = Labels.datosDeSiniestro.badge;
    this.labels = {
      tipo_cita: Labels.datosDeSiniestro.tipo_cita,
      cita_sinprogramar: Labels.datosDeSiniestro.cita_sinprogramar,
      cita_programada: Labels.datosDeSiniestro.cita_programada,
      fecha_ocurrido: Labels.datosDeSiniestro.fecha_ocurrido,
      fecha_atencion: Labels.datosDeSiniestro.fecha_atencion,
      hora_ocurrido: Labels.datosDeSiniestro.hora_ocurrido,
      hora_atencion: Labels.datosDeSiniestro.hora_atencion,
      lugar_siniestro: Labels.datosDeSiniestro.lugar_siniestro,
      tipo_vialidad: Labels.datosDeSiniestro.tipo_vialidad,
      nombre_calle: Labels.datosDeSiniestro.nombre_calle,
      num_exterior: Labels.datosDeSiniestro.num_exterior,
      num_interior: Labels.datosDeSiniestro.num_interior,
      cod_postal: Labels.datosDeSiniestro.cod_postal,
      colonia: Labels.datosDeSiniestro.colonia,
      municipio: Labels.datosDeSiniestro.municipio,
      estado: Labels.datosDeSiniestro.estado,
      calle1_tipo: Labels.datosDeSiniestro.calle1_tipo,
      calle1_nombre: Labels.datosDeSiniestro.calle1_nombre,
      calle2_tipo: Labels.datosDeSiniestro.calle2_tipo,
      calle2_nombre: Labels.datosDeSiniestro.calle2_nombre,
      tipo_carretera: Labels.datosDeSiniestro.tipo_carretera,
      nombre_carretera: Labels.datosDeSiniestro.nombre_carretera,
      num_kilometro: Labels.datosDeSiniestro.num_kilometro,
      observaciones: Labels.datosDeSiniestro.observaciones,
      btnNext: Labels.btn.next,
      btnCancel: Labels.btn.cancel
    };
  }
  openModal(content) {
    this.modalService.open(content, { centered: true });
  }

  onSubmit() { 
    
  }
}