import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';

// MODULES
// import { RegistroModule } from '../../registro.module';
// SERVICES
import * as configuracion from '../../../global/services/configurations';

@Injectable({
  providedIn: 'root'
})
export class RegistroService {
  config:any;

  constructor(private http: HttpClient) { 
    this.config = configuracion.ENVIROMENT;

  }

  getDataPost(data?:any): Observable<any> {
		return this.http.post<any>(
      this.config.url + '/test',
      { data: data ? data : {} },
    );
  }

  getDataGet(data?:any): Observable<any> {
    return of(configuracion.ULTIMA_VERSION_APP);

      // return this.http.get<any>(
      //     this.config.url + '/test001'
      // );
  }

  /**
	 * serviceGet
   * ----------------------------------------------------------------------
   * Utilizada para llamar al servicio mediante GET
   * @param   data      string 
	 * @since 	0.0.1
	 * @return 	response
	 */
  serviceGet(data?:string): Observable<any> {

    if(data == 'request_cat_causas'){
      return of(configuracion.REGISTRO_CATEGORIAS_CAUSA);
    }else if(data == 'request_cat_coberturas'){
      return of(configuracion.REGISTRO_CATEGORIAS_COBERTURA);
    }
    // return this.http.get<any>(
    //   this.config.url + '/' + data
    // );
  }

}
