import { Component, OnInit } from '@angular/core';
import { Router, Route, ActivatedRoute, ParamMap } from "@angular/router";
import { switchMap } from 'rxjs/operators';
import { FactoryService } from '../../services/local/factory/factory.service';
import { StorageService } from '../../services/local/storage/storage.service';
import { FormatterService } from '../../services/local/formatter/formatter.service';

import * as dataConfig from '../../../global/services/configurations';

@Component({
  selector: 'app-principal-flujo-registro',
  templateUrl: './principal-flujo-registro.component.html',
  styleUrls: ['./principal-flujo-registro.component.scss']
})
export class PrincipalFlujoRegistroComponent implements OnInit {

  dataStepper: any;
  mediaCss: string = "hiden-md";

  constructor(
    private factoryService:FactoryService,
    private storageService:StorageService,
    private formatterService:FormatterService,
    public route:ActivatedRoute) {

      

    // SUBSCRIBE
    this.storageService.updateRegistro().subscribe(
      data => {      
        this.asignarDatosAFlujo(data);
      }
    );

  }

  ngOnInit() {}

  asignarDatosAFlujo(data){
    this.formatterService.eliminarDataSeccion(data).then(
      data => {
        this.dataStepper = data;
      }
    );
  }

}
