import { Injectable } from '@angular/core';
import * as _ from 'lodash';    
import { StorageService } from '../storage/storage.service';

@Injectable({
  providedIn: 'root'
})
export class FormatterService {

  constructor(
    private storageService:StorageService
  ) { }

  eliminarDataSeccion(data){
    let arraySecciones: any[] = [];

    
      if(data){
        _.each(data, (obj) => {
          let promise = new Promise((resolve, reject) => {
            resolve(_.omit(obj, ['data']));
          });
          arraySecciones.push(promise);
        });

      }
    
    return Promise.all(arraySecciones);
  }

  actualizarSeccionActiva(sec){
    let data = this.storageService.getRegistro();

    _.each(data, (value, key, obj) => {
        data[key].estatus['esActiva'] = false;
    });

    data[sec].estatus['esActiva']     = true;
    data[sec].estatus['esHabilitada'] = true;

    this.storageService.setRegistro(data);
  }
  actualizarSubSeccionActiva(sec){
    let secParent = this.storageService.updateFlagSubFlujo();
    let data = this.storageService.getRegistro();

    _.each(data[secParent].data, (value, key, obj) => {
        data[secParent].data[key].estatus['esActiva'] = false;
    });

    data[secParent].data[sec].estatus['esActiva']     = true;
    data[secParent].data[sec].estatus['esHabilitada'] = true;

    this.storageService.setRegistro(data);
  }

  actualizarSeccionCompleta(sec, dataSec){
    let data = this.storageService.getRegistro();

    data[sec].data = dataSec;
    
    _.each(data, (value, key, obj) => {
      data[key].estatus['esActiva'] = false;
  });

    _.each(data[sec].estatus, (value, key, obj) => {
      data[sec].estatus[key] = true;
    });

    this.storageService.setRegistro(data);
  }
  actualizarSubSeccionCompleta(sec, dataSec){
    let secParent = this.storageService.updateFlagSubFlujo();
    let data = this.storageService.getRegistro();

    data[secParent].data[sec].data = dataSec;

    _.each(data[secParent].data[sec].estatus, (value, key, obj) => {
      data[secParent].data[sec].estatus[key] = true;
    });

  }


  recuperarSeccion(propertyData){

  }
  recuperarSubSeccion(propertyParent, propertyData){
    
  }

  asignarRutaUrl(data, value, params){
    let cloneObj = _.cloneDeep(data);

    _.each(cloneObj, (obj) => {
      obj.url += '/' + value;
      obj.params = params;
    });

    return cloneObj;
  }

  


}
