import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { NgAnalyzeModulesHost } from '@angular/compiler';
import { Router } from '@angular/router';
import * as Labels from '../../interfaces/labels';
import * as Inputs from '../../interfaces/inputs';

@Component({
  selector: 'app-causa-no-colision',
  templateUrl: './causa-no-colision.component.html',
  styleUrls: ['./causa-no-colision.component.scss']
})
export class CausaNoColisionComponent implements OnInit {
  
  // labels 
  header:string;
  badge:string;
  labels:any;
  inputs:any;

  @Output() dataForm = new EventEmitter();
  @Output() reset    = new EventEmitter();

  groupForm:FormGroup;

  constructor(public formBuilder: FormBuilder, private router: Router) {
    // inputs 
    this.inputs = {
      folio: Inputs.causaNoColision.folio,
      siniestro: Inputs.causaNoColision.siniestro
    };
    
    // validaciones
    this.groupForm = formBuilder.group({
      [this.inputs.folio]:      ['', Validators.required],
      [this.inputs.siniestro]:  ['', Validators.required],
    });
  }

  ngOnInit() {
    // labels
    this.header = Labels.causaNoColision.header;
    this.badge  = Labels.causaNoColision.badge;
    this.labels = {
      folio: Labels.causaNoColision.folio,
      siniestro: Labels.causaNoColision.siniestro,
      btnNext: Labels.btn.next,
      btnCancel: Labels.btn.cancel
    };
  }
  
  onSubmit(){
    this.router.navigate([
      '/main-dashboard/registro-en-linea/principal-registro/registro-inicio',
      // { outlets: { registroEnLinea: ['datos-siniestro'] } }
    ]);
  }
  
  clear() {
    this.groupForm.reset();
    this.reset.emit();
  }
}
