import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { FormatterService } from '../../services/local/formatter/formatter.service';
import { StorageService } from '../../services/local/storage/storage.service';

@Component({
  selector: 'app-circunstancia',
  templateUrl: './circunstancia.component.html',
  styleUrls: ['./circunstancia.component.scss']
})
export class CircunstanciaComponent implements OnInit {

  public groupForm: FormGroup;
  otro:boolean = false;
  tipo:boolean = false;

  constructor(
    private formatterService:FormatterService,
    private storageService:StorageService,
    private formBuilder: FormBuilder) {}

  ngOnInit() {
    this.formatterService.actualizarSubSeccionActiva('seccionCircunstancias');

    this.groupForm = this.formBuilder.group({
      circunstancia: '',
      acuerdo: false,
    });
  }

  selectChangeHandler(event: any) {
    // si es otro
    if (event.target.value == 10) {
      this.otro = true;
    } else {
      this.otro = false;
    }
  }
  
  selectChangeAc(event: any) {
    // si es acuerdo
    this.tipo = event.target.value;
  }
  
  onSubmit(){
    var element: HTMLElement = document.getElementById('fotos') as HTMLElement;
    element.click();
  }
}
