import { Injectable } from '@angular/core';
import * as Registro from '../../../classes/registro';
import { FormatterService } from '../formatter/formatter.service';
import * as config from '../../../../global/services/configurations';

@Injectable({
  providedIn: 'root'
})
export class FactoryService {

  constructor(private formatterService:FormatterService) { }

  construirRegistroColision(){
    let catalogoSecciones:any    = config.SECCIONES;

    let seccionInicio            = new Registro.SeccionInicio(catalogoSecciones.inicio, {});

    let seccionSiniestro         = new Registro.SeccionSiniestro(catalogoSecciones.siniestro, {});
    let seccionPoliza            = new Registro.SeccionPoliza(catalogoSecciones.poliza, {});
    let seccionAsegurado         = new Registro.SeccionAsegurado(catalogoSecciones.asegurado, {});
    let seccionConductor         = new Registro.SeccionConductor(catalogoSecciones.conductor, {});
    let seccionVehiculo          = new Registro.SeccionVehiculo(catalogoSecciones.vehiculo, {});
    let seccionInvolucrados      = new Registro.SeccionInvolucrado(catalogoSecciones.involucrados, {});
    let seccionNarracion         = new Registro.SeccionNarracion(catalogoSecciones.narracion, {});
    let seccionPuntosDeChoque    = new Registro.SeccionPuntosChoque(catalogoSecciones.puntosChoque, {});
    let seccionCircunstancias    = new Registro.SeccionCircunstancias(catalogoSecciones.circunstancias, {});
    let seccionFotos             = new Registro.SeccionFotos(catalogoSecciones.fotos, {});
    let seccionCroquis           = new Registro.SeccionCroquis(catalogoSecciones.croquis, {});
    
    let seccionGeneracionOrdenes = new Registro.SeccionGeneracionOrdenes(catalogoSecciones.generacionOrdenes, {});
    let seccionTerminoAtencion   = new Registro.SeccionTerminoAtencion(catalogoSecciones.terminoAtencion, {});
    let seccionInformeAjustador  = new Registro.SeccionInformeAjustador(catalogoSecciones.informeAjustador, {});

    let subSecciones             = new Registro.SubSeccionesClass(seccionSiniestro, seccionPoliza, seccionAsegurado, seccionConductor, seccionVehiculo, seccionInvolucrados, seccionNarracion, seccionPuntosDeChoque, seccionCircunstancias, seccionFotos, seccionCroquis);

    let subSeccionesAfectado     = this.formatterService.asignarRutaUrl(subSecciones, 'afectado','seccionAfectado');
    let subSeccionesResponsable  = this.formatterService.asignarRutaUrl(subSecciones, 'responsable','seccionResponsable');

    let seccionResponsable       = new Registro.SeccionInvolucrado(catalogoSecciones.responsable, subSeccionesResponsable);
    let seccionAfectado          = new Registro.SeccionInvolucrado(catalogoSecciones.afectado, subSeccionesAfectado);
    
    let registroColision         = new Registro.RegistroColision(seccionInicio, seccionResponsable, seccionAfectado,seccionGeneracionOrdenes, seccionTerminoAtencion, seccionInformeAjustador);
    
    return registroColision;
  }
}
