import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { FormatterService } from '../../services/local/formatter/formatter.service';
import { StorageService } from '../../services/local/storage/storage.service';

import {NgbModal, ModalDismissReasons, NgbAlertConfig } from '@ng-bootstrap/ng-bootstrap';
import * as _ from 'lodash';    

@Component({
  selector: 'app-fotos',
  templateUrl: './fotos.component.html',
  styleUrls: ['./fotos.component.scss']
})
export class FotosComponent implements OnInit {
  modal: any;
  public groupForm: FormGroup;
  verOpcionesCaracteristica:any;
  alerta: any = {view: false, id: null};
  verImagen: any;
  cabeceras: any[] = [
    {
      nombre: 'Caracterstica',
    },
    {
      nombre: 'Nmero de fotos',
    },
    {
      nombre: '',
    },
    {
      nombre: '',
    }
  ];
  caracteristicas: any[] = [
    {
      id: 1,
      nombre: 'Automvil/Siniestro',
      imagenes: []
    },
    {
      id: 2,
      nombre: 'Daos',
      imagenes: []
    },
    {
      id: 3,
      nombre: 'Documentos',
      imagenes: []
    },
    {
      id: 4,
      nombre: 'VIN',
      imagenes: []
    },
    {
      id: 5,
      nombre: 'Crucero',
      imagenes: []
    },
    {
      id: 6,
      nombre: 'Lesiones',
      imagenes: []
    },
    {
      id: 7,
      nombre: 'Otros',
      imagenes: []
    }
  ];
  constructor(
    private formatterService:FormatterService,
    private storageService:StorageService,
    private formBuilder: FormBuilder,
    private modalService: NgbModal) {}

  ngOnInit() {
    this.formatterService.actualizarSubSeccionActiva('seccionFotos');

    this.groupForm = this.formBuilder.group({
      conductor_igual_asegurado: true,
    });

    this.caracteristicas.map((item, index) => {
      item.alert = { view: false, type: null, text: []};
    });
  }
  
  onSubmit(){
    var element: HTMLElement = document.getElementById('croquis') as HTMLElement;
    element.click();
  }

  cerrarAlertaMensaje(id){
    this.caracteristicas.forEach(item => {
      if(item.id == id){
        item.alert.view = false;
      }
    });
  }

  onFileChanged(event, id) {

    this.buildImages(event.target.files).then(
      data => {
        this.caracteristicas.forEach(item => {
          if(item.id == id){
            item.imagenes = [ ...data, ...item.imagenes];
            item.alert = this.buildAlert(data);

            _.remove(item.imagenes, (img) => {
              return !img.isValid;
            });

          }
        });

      },
      err => {}
    );
    
  }

  buildAlert(items):any{
    let alert = { view: true, type: null , text: [] };
    let itemsInvalid: string[] = [];
    let noItemsValid: number = 0;
    let text: string[] = [];

    items.forEach(item => {
      if(!item.isValid) {
        itemsInvalid.push(`<i>- "Archivo ${item.nombre}"</i><b> no valido.</b><br>`);
      }else{
        noItemsValid++;
      };
    });
    
    if(noItemsValid)text.push(`Se cargaron exitosamente ${noItemsValid} archivo(s).<br>`);
    if(itemsInvalid.length != 0)text.push(`<b>Error:</b><br>`);

    alert.type = ( itemsInvalid.length != 0 ) ? 'danger' : 'success';
    alert.text = [ ...text, ...itemsInvalid];

    return alert;
  }

  isValidFile(typeParam):Boolean{
    let fomatosPermitidos: any[] = ['image/jpg', 'image/jpge', 'image/png'];
    let isValid: boolean = false;

    fomatosPermitidos.forEach(type => { if(type == typeParam) isValid = true } );

    return isValid;
    
  }

  buildImages(files){
    let arrayPromises = [];

      for(let i=0; i< files.length; i++){
        
        let promise = new Promise((resolve, reject) => {
          let objImage = { isValid: null, data: null, id: null, nombre: null, tipo: null, size: null, url: null };
          let reader = new FileReader();

          if(this.isValidFile(files[i].type)){
            reader.readAsDataURL(files[i]);
            reader.onload = (event:any) => {
              objImage.isValid = true;
              objImage.url = event.target.result;
              objImage.nombre = files[i].name;
              objImage.size = files[i].size;
              objImage.tipo = files[i].type;
              objImage.data = files[i];
              objImage.id = i;
      
              resolve(objImage);
            };
          }
          else{
            objImage.isValid = false;
            objImage.nombre = files[i].name;

            resolve(objImage);
          }

          

        });

        arrayPromises.push(promise);
      
      }

    return Promise.all(arrayPromises);

  }

  verOpcionesImagenes(modal, item){
    this.cerrarAlertaMensaje(item.id);

    this.verOpcionesCaracteristica = item;

    this.modal = this.modalService.open(modal, {backdrop:'static', size:'lg'});
  }

  eliminarImagenesTodas(id){
    this.cerrarAlertaMensaje(id);

    this.caracteristicas.forEach(item => {
      _.remove(item.imagenes, () => {
        return item.id == id;
      });
    });
  }

  calcularTotalImagenes(){
    let total = 0;

    this.caracteristicas.forEach(item => {
      total += item.imagenes.length;
    });

    return total;

  }  

  eliminarImagen(id){
    _.remove(this.verOpcionesCaracteristica.imagenes, (img) => {
      return img.id == id;
    });

    if(this.verImagen && id == this.verImagen.id)this.verImagen = null;

  }
  verImagenCat(img){
    this.verImagen = img;
  }
  closeAlert(){};


  cerrarModal(){
    console.log("==>> CERRANDO MODAL....");
    this.verImagen = null;
    this.verOpcionesCaracteristica = null;
    this.modal.close();
  }

  aceptarModal(){
    console.log("==>> OK MODAL DISMISS ....");
    this.verImagen = null;
    this.verOpcionesCaracteristica = null;
    this.modal.dismiss();
  }
  

}
