import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { FormatterService } from '../../services/local/formatter/formatter.service';

@Component({
  selector: 'app-termino-de-atencion',
  templateUrl: './termino-de-atencion.component.html',
  styleUrls: ['./termino-de-atencion.component.scss']
})
export class TerminoDeAtencionComponent implements OnInit {

  public groupForm: FormGroup;
  
  constructor(
    private formBuilder: FormBuilder,
    private formatterService:FormatterService) {

      this.formatterService.actualizarSeccionActiva('seccionTerminoAtencion');
      
    }

  ngOnInit() {
    this.groupForm = this.formBuilder.group({

    });
  }
  
  onSubmit(){
    var element: HTMLElement = document.getElementById('informe-ajustador') as HTMLElement;
    element.click();
  }
}
