import { Directive, Input, TemplateRef, ViewContainerRef } from '@angular/core';

import * as config from '../services/configurations';

@Directive({
    selector: '[appCanAccess]'
  })
  export class CanAccessDirective {

    constructor(
      private templateRef: TemplateRef<any>,
      private viewContainer: ViewContainerRef) {
    }

    @Input() set appCanAccess(conditions: any[]) {
      let permisos: any = config.CONTENIDO_ROLES_PERMISOS;
      let usuario:  any = config.USUARIO;
      let canAccess: boolean = false;

      console.log("==>>> DIRECTIVA appCanAccess: PARASMS " + conditions[0] + conditions[1]);
      
      permisos[conditions[0]][conditions[1]].permisosRol.forEach(rol => {
        if(rol == usuario.rol) canAccess = true;
      });

      if (canAccess) {
        this.viewContainer.createEmbeddedView(this.templateRef);
      } else{
        this.viewContainer.clear();
      }
    }
  
  }