import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// PAGES
import { SkeletonDashboardComponent } from '../global/pages/skeleton-dashboard/skeleton-dashboard.component';
import { SegundaAtencionComponent } from './pages/segunda-atencion/segunda-atencion.component';

const routes: Routes = [
  {
    path:'main-dashboard',
    component: SkeletonDashboardComponent,
    children: [
      { 
        path: 'segunda-atencion', 
        component: SegundaAtencionComponent,
        data: { breadcrumb: 'Segunda atencin' }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ComplementosRoutingModule { }
