import { Component, OnInit} from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { RegistroService } from '../../services/remote/registro.service';
import { FactoryService } from '../../services/local/factory/factory.service';
import { StorageService } from '../../services/local/storage/storage.service';
import { FormatterService } from '../../services/local/formatter/formatter.service';
import { FLUJO_REGISTRO_SINIESTRO } from '../../../global/services/configurations';
import { switchMap, distinctUntilChanged } from 'rxjs/operators';

@Component({
  selector: 'app-flujo-registro',
  templateUrl: './flujo-registro.component.html',
  styleUrls: ['./flujo-registro.component.scss']
})
export class FlujoRegistroComponent implements OnInit {

  itemsFlujo: any[];
  mediaCss: string = "hiden-lg";
  params: any;
  obvParam: any;

  constructor(
    private router: Router,
    public route:ActivatedRoute,
    private RegistroService:RegistroService,
    private factoryService:FactoryService,
    private storageService:StorageService,
    private formatterService:FormatterService) {
    
      this.storageService.updateRegistro().subscribe(
        data => {
          this.asignarDatosAFlujo(data);
        }
      );
      
      
    }


  ngOnInit() {
    this.params = this.route.snapshot.paramMap.get('params');
    this.formatterService.actualizarSeccionActiva(this.params);
    this.storageService.setFlagSubFlujo(this.params);
  }

  ngOnDestroy(){}


  asignarDatosAFlujo(data){
    this.formatterService.eliminarDataSeccion(data[this.params].data).then(
      data => {
        this.itemsFlujo = data;
      }
    );
  }

}
