export interface Estado{
    esActiva    :boolean;
    esCompleta  :boolean;
    esHabilitada:boolean;
}
interface SubSeccionesInterface{
    seccionSiniestro     :SeccionSiniestro,
    seccionPoliza        :SeccionPoliza,
    seccionAsegurado     :SeccionAsegurado,
    seccionConductor     :SeccionConductor,
    seccionVehiculo      :SeccionVehiculo,
    seccionInvolucrados  :SeccionInvolucrados,
    seccionNarracion     :SeccionNarracion,
    seccionPuntosDeChoque:SeccionPuntosChoque,
    seccionCircunstancias:SeccionCircunstancias,
    seccionFotos         :SeccionFotos,
    seccionCroquis       :SeccionCroquis,
}
export class SubSeccionesClass{
    seccionSiniestro     :SeccionSiniestro;
    seccionPoliza        :SeccionPoliza;
    seccionAsegurado     :SeccionAsegurado;
    seccionConductor     :SeccionConductor;
    seccionVehiculo      :SeccionVehiculo;
    seccionInvolucrados  :SeccionInvolucrados;
    seccionNarracion     :SeccionNarracion;
    seccionPuntosDeChoque:SeccionPuntosChoque;
    seccionCircunstancias:SeccionCircunstancias;
    seccionFotos         :SeccionFotos;
    seccionCroquis       :SeccionCroquis;

    constructor(seccionSiniestro, seccionPoliza, seccionAsegurado, seccionConductor, seccionVehiculo, seccionInvolucrados, seccionNarracion, seccionPuntosDeChoque, seccionCircunstancias, seccionFotos, seccionCroquis ){
        this.seccionSiniestro = seccionSiniestro;
        this.seccionPoliza = seccionPoliza;
        this.seccionAsegurado = seccionAsegurado;
        this.seccionConductor = seccionConductor;
        this.seccionVehiculo = seccionVehiculo;
        this.seccionInvolucrados = seccionInvolucrados;
        this.seccionNarracion = seccionNarracion;
        this.seccionPuntosDeChoque = seccionPuntosDeChoque;
        this.seccionCircunstancias = seccionCircunstancias;
        this.seccionFotos = seccionFotos;
        this.seccionCroquis = seccionCroquis;
    }
}

export class Seccion{
    id     :number;
    nombre :string;
    icono  :string;
    url    :string;
    path   :string;
    params :string;
    estatus:Estado;

    constructor(id, nombre, icono, url, path, params, estatus){
        this.id      = id;
        this.nombre  = nombre;
        this.icono   = icono;
        this.url     = url;
        this.path    = path;
        this.params  = params;
        this.estatus = estatus;
    }
}

// SECCIONES
export class SeccionInicio extends Seccion{
    data:DatosInicio;

    constructor(seccion, data){
        super(seccion.id, seccion.nombre, seccion.icono, seccion.url, seccion.path, seccion.params, seccion.estatus);
        this.data = data;
    }
}
