// GLOBAL
export class RegistroColision{
    seccionInicio           :SeccionInicio;
    seccionResponsable      :SeccionInvolucrado;
    seccionAfectado         :SeccionInvolucrado;
    seccionGeneracionOrdenes:SeccionGeneracionOrdenes;
    seccionTerminoAtencion  :SeccionTerminoAtencion;
    seccionInformeAjustador :SeccionInformeAjustador;

    constructor(inicio, responsable, afectado, generacionOrdenes, terminoAtencion, informeAjustador){
        this.seccionInicio            = inicio;
        this.seccionResponsable       = responsable;
        this.seccionAfectado          = afectado;
        this.seccionGeneracionOrdenes = generacionOrdenes;
        this.seccionTerminoAtencion   = terminoAtencion;
        this.seccionInformeAjustador  = informeAjustador;
    }

}
