import { Component, OnInit, EventEmitter, Output} from '@angular/core';
import * as $ from 'jquery/dist/jquery';

// interfaces
import { FsDocument } from '../../interfaces/fs-document';
import { FsDocumentElement } from '../../interfaces/fs-document-element';

@Component({
  selector: 'app-barra-superior',
  templateUrl: './barra-superior.component.html',
  styleUrls: ['./barra-superior.component.scss']
})
export class BarraSuperiorComponent implements OnInit {
  @Output() clickButtonMenu = new EventEmitter<boolean>();

  constructor() {}

  ngOnInit() {
  }
  
  clickBtnMenu() {
    $("body").toggleClass("enlarged");
    this.clickButtonMenu.emit($('body').hasClass('enlarged'));
  }

  isFullScreen(): boolean {
    const fsDoc = <FsDocument> document;
    
    return !!(fsDoc.fullscreenElement 
      || fsDoc.mozFullScreenElement 
      || fsDoc.webkitFullscreenElement 
      || fsDoc.msFullscreenElement);
  }

  toggleFullScreen(): void {
    var fsDoc = <FsDocument> document;
  
    if (!this.isFullScreen()) {
      var fsDocElem = <FsDocumentElement> document.documentElement;
  
      if (fsDocElem.requestFullscreen)
        fsDocElem.requestFullscreen();

      else if (fsDocElem.msRequestFullscreen)
        fsDocElem.msRequestFullscreen();

      else if (fsDocElem.mozRequestFullScreen)
        fsDocElem.mozRequestFullScreen();

      else if (fsDocElem.webkitRequestFullscreen)
        fsDocElem.webkitRequestFullscreen();
    }
    else if (fsDoc.exitFullscreen)
      fsDoc.exitFullscreen();

    else if (fsDoc.msExitFullscreen)
      fsDoc.msExitFullscreen();

    else if (fsDoc.mozCancelFullScreen)
      fsDoc.mozCancelFullScreen();
      
    else if (fsDoc.webkitExitFullscreen)
      fsDoc.webkitExitFullscreen();
  }
}
