import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';
import { FormatterService } from '../../services/local/formatter/formatter.service';
import { StorageService } from '../../services/local/storage/storage.service';

@Component({
  selector: 'app-busqueda-de-poliza',
  templateUrl: './busqueda-de-poliza.component.html',
  styleUrls: ['./busqueda-de-poliza.component.scss']
})
export class BusquedaDePolizaComponent implements OnInit {

  public groupForm: FormGroup;
  change:boolean = false;

  constructor(
    public formBuilder: FormBuilder,
    private router: Router,
    private modalService: NgbModal,
    private formatterService:FormatterService,
    private storageService:StorageService,) {
      console.log("EN SUB-FLUJO/BUSQUEDA DE POLIZA...");

    }

  ngOnInit() {
    this.formatterService.actualizarSubSeccionActiva('seccionPoliza');

    this.groupForm = this.formBuilder.group({
      tipo_busqueda: false,
    });
  }

  selectChange(event: any) {
    // si es acuerdo
    this.change = event.target.value;
  }

  openModal(content) {
    this.modalService.open(content, { centered: true });
  }

  onSubmit(){ 
    this.router.navigate([
      '/main-dashboard/registro-en-linea/flujo-registro',
      { outlets: { registroEnLinea: ['datos-poliza'] } }
    ]);
  }
}