import { Component,HostListener } from '@angular/core';
import * as $ from 'jquery/dist/jquery';
import * as config from '../../services/configurations';

@Component({
  selector: 'skeleton-dashboard',
  templateUrl: './skeleton-dashboard.component.html',
  styleUrls: ['./skeleton-dashboard.component.scss']
})
export class SkeletonDashboardComponent {

  menu: any[];
    
  constructor() {
    this.menu = config.MENU;
  }

  changeMenu(ev:boolean){
    if(ev){
      this.menu.forEach(item => {
        if(item.childs) item.isCollapsed = false;
      });
    }
  }

  ngOnInit() {
    if ($(document).width() < 1024){
      $('body').addClass('enlarged');
    }
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (event.target.innerWidth < 1024){
      $('body').addClass('enlarged');
      this.changeMenu(true);
    } else {
      $('body').removeClass('enlarged');
    }
  }
}