import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import * as Labels from '../../interfaces/labels';
import * as Inputs from '../../interfaces/inputs';

@Component({
  selector: 'app-causa-colision',
  templateUrl: './causa-colision.component.html',
  styleUrls: ['./causa-colision.component.scss']
})
export class CausaColisionComponent implements OnInit {

  // labels 
  header:any;
  badge:any;
  labels:any;
  inputs:any;

  @Output() dataForm = new EventEmitter();
  @Output() reset    = new EventEmitter();
  
  groupForm:FormGroup;

  constructor(public formBuilder: FormBuilder, private router: Router) {
    // inputs
    this.inputs = {
      folio: Inputs.causaColision.folio,
      rol: Inputs.causaColision.rol,
      companiaPropio: Inputs.causaColision.companiaPropio,
      siniestroPropio: Inputs.causaColision.siniestroPropio,
      companiaContraparte: Inputs.causaColision.companiaContraparte,
      siniestroContraparte: Inputs.causaColision.siniestroContraparte
    };

    // validaciones
    this.groupForm = formBuilder.group({
      [this.inputs.folio]: ['', Validators.required],
      [this.inputs.rol]:  ['', Validators.required],
      [this.inputs.companiaPropio]: ['', Validators.required],
      [this.inputs.siniestroPropio]: ['', Validators.required],
      [this.inputs.companiaContraparte]: ['', Validators.required],
      [this.inputs.siniestroContraparte]: ['', Validators.required]  
    });
  }

  ngOnInit() {
    // labels
    this.header = Labels.causaColision.header;
    this.badge  = Labels.causaColision.badge;
    this.labels = {
      folio: Labels.causaColision.folio,
      rol: Labels.causaColision.rol, 
      compania: Labels.causaColision.compania,
      siniestro: Labels.causaColision.siniestro,
      btnNext: Labels.btn.next,
      btnCancel: Labels.btn.cancel
    };
  }

  onSubmit() {
    //this.dataForm.emit(this.groupForm);
    this.router.navigate([
      '/main-dashboard/registro-en-linea/principal-registro/registro-inicio',
      // { outlets: { registroEnLinea: ['datos-siniestro'] } }
    ]);
  }

  clear() {
    this.groupForm.reset();
    this.reset.emit();
  }
}
