import { Component, OnInit }  from '@angular/core';
import { RegistroService }    from '../../services/remote/registro.service';
import { FactoryService } from '../../services/local/factory/factory.service';
import { StorageService } from '../../services/local/storage/storage.service';

@Component({
  selector: 'app-registro-en-linea',
  templateUrl: './registro-en-linea.component.html',
  styleUrls: ['./registro-en-linea.component.scss']
})
export class RegistroEnLineaComponent implements OnInit {

  reset:any          = false;
  colision:boolean   = false;
  nocolision:boolean = false;
  dataCausas:any;
  dataCoberturas:any;

  constructor(
    private factoryService:FactoryService,
    private storageService:StorageService,
    public registroService:RegistroService) {

    // CONSTRUIR OBJETO REGISTRO
    let registro = this.factoryService.construirRegistroColision();
    // ALMACENAR OBJETO REGISTRO
    this.storageService.setRegistro(registro);

  }

  ngOnInit() {
    this.requestCatCoberturas();
    this.requestCatCausas();
  }

  requestCatCausas() {
    this.registroService.serviceGet('request_cat_causas').subscribe(
      success => {
        this.dataCausas = success;
      },
      err => {}
    );
    return this.dataCausas;
  }
  
  requestCatCoberturas() {
    this.registroService.serviceGet('request_cat_coberturas').subscribe(
      success => {
        this.dataCoberturas = success;
      },
      err => {}
    );
    return this.dataCoberturas;
  }

  validationInicio(data): void {
    var request = data.value.input_causa;

    if(request == 1 || request == 3 || request == 5) {
      this.colision = true;
    // no colisin
    } else this.nocolision = true;
  }

  clear(data): void {
    this.reset = Math.random();
    this.colision = false;
    this.nocolision = false;
  }
}