import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import * as Labels from '../../interfaces/labels';
import * as Inputs from '../../interfaces/inputs';

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.scss']
})
export class InicioComponent implements OnChanges, OnInit {

  // Entrada de datos
  @Input() catCausas:any;
  @Input() catCoberturas:any;
  @Input() reset:boolean = false;

  @Output() dataForm = new EventEmitter();

  // labels 
  header:string;
  badge:string;
  labels:any;
  inputs:any;

  // hidden
  disabledSubmit:boolean = false;
  
  inicioForm:FormGroup;

  constructor(public formBuilder: FormBuilder) {
    // inputs
    this.inputs = {
      causa: Inputs.inicioRegistro.causa,
      cobertura: Inputs.inicioRegistro.cobertura
    }

    // validaciones
    this.inicioForm = formBuilder.group({
      [this.inputs.causa]:     ['', Validators.required],
      [this.inputs.cobertura]: ['', Validators.required]
    });
  }

  onSubmit() {
    this.dataForm.emit(this.inicioForm);
    this.disabledSubmit = true;
  }

  ngOnInit(): void {    
    // labels
    this.header = Labels.inicioRegistro.header;
    this.badge  = Labels.inicioRegistro.badge;
    this.labels = {
      causa: Labels.inicioRegistro.causa,
      cobertura: Labels.inicioRegistro.cobertura,
      btnNext: Labels.btn.next
    };
  }

  ngOnChanges(changes: SimpleChanges): void {
    let reset = changes.reset;
    if (reset !== undefined) {
      if (reset) this.disabledSubmit = false;
    }
  }
}
