import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// PAGES
import { InicioSesionComponent } from './pages/inicio-sesion/inicio-sesion.component';
import { NoticiasComponent } from './pages/noticias/noticias.component';
import { SkeletonDashboardComponent } from '../global/pages/skeleton-dashboard/skeleton-dashboard.component';
import { TableroDeOperacionesComponent } from './pages/tablero-de-operaciones/tablero-de-operaciones.component';
import { AcercaDeComponent } from './pages/acerca-de/acerca-de.component';

const routes: Routes = [
  { 
    path: 'inicio-sesion', 
    component: InicioSesionComponent,        
    data: { breadcrumb: 'Inicio de sesin' }
  },
  {
    path:'main-dashboard',
    component: SkeletonDashboardComponent,
    children: [
      { 
        path: 'noticias-edua', 
        component: NoticiasComponent,
        data: { breadcrumb: 'Noticias eDUA' }
      },
      { 
        path: 'tablero-operaciones', 
        component: TableroDeOperacionesComponent,
        data: { breadcrumb: 'Tablero de operaciones' }
      },
      { 
        path: 'acerca-de', 
        component: AcercaDeComponent,
        data: { breadcrumb: 'Acerca de' }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class InicioRoutingModule { }
